'use client';

import React from 'react';
import Link from 'next/link';
import { usePathname } from 'next/navigation';
import {
    Gamepad2, LayoutGrid, Users, Calendar,
    ShoppingBag, FileText, LogOut, Settings
} from 'lucide-react';
import { useAuth } from './auth-provider';

export function Sidebar() {
    const pathname = usePathname();
    const { logout } = useAuth();

    const menuItems = [
        { label: 'Dashboard', icon: LayoutGrid, path: '/dashboard' },
        { label: 'Meja & Billing', icon: Gamepad2, path: '/tables' },
        { label: 'Membership', icon: Users, path: '/members' },
        { label: 'POS & Menu', icon: ShoppingBag, path: '/pos' },
        { label: 'Reservasi', icon: Calendar, path: '/reservations' },
        { label: 'Laporan', icon: FileText, path: '/reports' },
    ];

    return (
        <aside className="w-64 bg-[#0f1a14] border-r border-[#1e3328] flex flex-col shrink-0 z-20 h-screen">
            {/* Brand */}
            <div className="p-6 border-b border-[#1e3328] flex items-center gap-3">
                <div className="size-10 bg-[#22c55e] rounded-lg flex items-center justify-center transform rotate-3 shadow-[0_0_15px_rgba(34,197,94,0.4)]">
                    <Gamepad2 size={24} className="text-black" />
                </div>
                <div>
                    <h1 className="text-xl font-bold text-white tracking-tight">Billiard<span className="text-[#22c55e]">OS</span></h1>
                    <p className="text-[10px] text-slate-400 font-mono tracking-widest uppercase">Management System</p>
                </div>
            </div>

            {/* Navigation */}
            <nav className="flex-1 p-4 space-y-1 overflow-y-auto">
                {menuItems.map((item) => {
                    const isActive = pathname.startsWith(item.path);
                    return (
                        <Link
                            key={item.path}
                            href={item.path}
                            className={`flex items-center gap-3 px-4 py-3 rounded-lg text-sm font-medium transition-all duration-200 group
                ${isActive
                                    ? 'bg-[#22c55e] text-black shadow-lg shadow-[#22c55e]/20'
                                    : 'text-slate-400 hover:text-white hover:bg-[#16261d]'
                                }
              `}
                        >
                            <item.icon size={20} className={isActive ? 'text-black' : 'group-hover:text-[#22c55e] transition-colors'} />
                            {item.label}
                        </Link>
                    );
                })}
            </nav>

            {/* Footer */}
            <div className="p-4 border-t border-[#1e3328]">
                <div className="bg-[#16261d] rounded-xl p-4 mb-4 border border-[#1e3328]">
                    <div className="flex items-center gap-3 mb-3">
                        <div className="size-8 rounded-full bg-slate-700"></div>
                        <div className="overflow-hidden">
                            <div className="text-sm font-bold text-white truncate">Staff</div>
                            <div className="text-xs text-slate-500 truncate">Cashier</div>
                        </div>
                    </div>
                    <button
                        onClick={() => logout()}
                        className="w-full flex items-center justify-center gap-2 text-xs font-bold text-[#ef4444] bg-[#ef4444]/10 hover:bg-[#ef4444]/20 py-2 rounded transition-colors"
                    >
                        <LogOut size={14} /> Keluar
                    </button>
                </div>
                <div className="text-center text-[10px] text-slate-600">
                    v1.0.0 â€¢ BilliardOS
                </div>
            </div>
        </aside>
    );
}
